import React from 'react';
import Icon from './Icon';
import {lang, doc} from '../../../lang/lang';
import { Row, Col, Card, Form, Button, InputGroup, FormControl, DropdownButton, Dropdown, Table } from 'react-bootstrap';

const Parametro = (promp) => {
    const {id, onLoad, delParam, param_id, param_de, param_tp, param_icon, param_txt, activo_idx, v_tp} = promp
    
    const x_id = ( param_id != undefined )?param_id:-1
    const x_de = ( param_de != undefined )?param_de:''
    const x_tp = ( param_tp != undefined )?param_tp:'0'
    const x_icon = ( param_icon != undefined )?param_icon:''
    const x_txt = ( param_txt != undefined )?param_txt:''
    const x_vtp = ( v_tp != undefined )?v_tp:'texto'
    let bg = (x_id % 2 == 0)?'#fafaff':''
    return (
        <React.Fragment> 
            <Row style={{backgroundColor: bg, borderBottom: 'solid 1px #cccccc'}}>
                <Col md={2}>
					<Form.Group controlId="formBasicEmail">
                        <Form.Label>{lang.nombre_param} {x_id +1}</Form.Label>
                        <Form.Control type="text" placeholder="Tipo de Activo" value={x_de} />
                    </Form.Group>
                </Col>
                <Col md={4}>
                    <Form.Group controlId="formBasicPassword">
                        <Form.Label>{lang.de_param}</Form.Label>
                        <Form.Control type="text" placeholder="Descripción" value={x_txt} />
                    </Form.Group>
                </Col>
                <Col md={3}>
                    <Form.Group controlId="exampleForm.ControlSelect1">
                        <Form.Label>{lang.icon_param}</Form.Label>
						<Icon id='icon1' img={x_icon} />
                	</Form.Group>
            	</Col>             
            	<Col md={1}>
                    <Form.Group controlId="exampleForm.ControlSelect1">
                        <Form.Label>{lang.dato_tp}</Form.Label>
                        <Form.Control as="select">
                            <option selected={(x_vtp=='texto')?'selected':''}>Texto</option>
                            <option selected={(x_vtp=='imagen')?'selected':''}>Imagen</option>
                            <option selected={(x_vtp=='documento')?'selected':''}>Documento</option>
                            <option selected={(x_vtp=='monto')?'selected':''}>Monto</option>
                            <option selected={(x_vtp=='numero')?'selected':''}>Número</option>
                            <option selected={(x_vtp=='fecha')?'selected':''}>Fecha</option>
							<option selected={(x_vtp=='boolean')?'selected':''}>Boolean</option>
                        </Form.Control>
                    </Form.Group>
                </Col>
                <Col md={1}>
                    <Form.Group controlId="formBasicChecbox">
                        <Form.Check type="checkbox" label={lang.obligatorio} checked={(x_tp==1)?'checked':''} />
                    </Form.Group>
                </Col>
				<Col md={1}>
				<i className='fa fa-trash f-30' style={{color: '#777'}} title={lang.eliminar} onClick={() => promp.delParam(promp.id)} /> 
                </Col>
                <hr />
            </Row>
        </React.Fragment> 
    );
};
export default Parametro;