import React, { useState } from 'react';
import { HuePicker } from 'react-color';
// AlphaPicker BlockPicker ChromePicker CirclePicker CompactPicker GithubPicker HuePicker MaterialPicker PhotoshopPicker SketchPicker SliderPicker SwatchesPicker TwitterPicker
import Parametro from './Parametro';
import Icon from './Icon';
import { Row, Col, Card, Form, Button, Tabs, Tab, Nav, InputGroup, FormControl, DropdownButton, Dropdown, Table } from 'react-bootstrap';
import Help from '../../../components/Help/Help';
import {lang, doc} from '../../../lang/lang';
import {aoa} from '../../../x';

const ParametroList = (promp) => {
    const {id} = promp
    const [huePickerColor, setHuePickerColor] = useState("#aaaaaa");
    const [parametros, setParametros] = useState([]);
    
    const [idc, setIdc] = useState(0);
    const delParam = (idc) => {setParametros(parametros.filter((item, i) => i != idc))}
    const upParam = () => {setParametros([...parametros, <Parametro id={idc} onLoad={setIdc(idc+1)} delParam={delParam} />])}
    
    let bag = []
    for(let j=0; j < aoa.activo_tps[id].detalle.length ;j++){
        let item = aoa.activo_tps[id].detalle[j]
        const x_id = ( item.id != undefined )?item.id:-1
        const x_de = ( item.de != undefined )?item.de:''
        const x_tp = ( item.tp != undefined )?item.tp:'0'
        const x_icon = ( item.icon != undefined )?item.icon:''
        const x_txt = ( item.txt != undefined )?item.txt:''  
        const x_vtp = ( item.valor != undefined && item.valor[item.valor.length -1].tp != undefined )?item.valor[item.valor.length -1].tp:'texto'
        bag.push(<Parametro key={j} id={j} onLoad={() => setIdc(j+1)} delParam={delParam} 
        param_id={x_id}
        param_de={x_de}
        param_tp={x_tp}
        param_icon={x_icon}
        param_txt={x_txt}
        activo_idx={id}
        v_tp={x_vtp}
        />)
        // pvalor_de={item.valor[item.valor.length].de}
        // valor: [{tp: '', de: '', fecha: ''}]
    }

   /*
                   id={activo_tps[i].id}
                idx={activo_tps[i].identificador}
                icon={activo_tps[i].icon}
                de={activo_tps[i].de} 
                color1={activo_tps[i].color[0]}
                color2={activo_tps[i].color[1]}
    */
                  
        return (
            <React.Fragment>
                {bag}
                <hr />  
            </React.Fragment> 
        );
};
export default ParametroList;