import React, { useState, useEffect } from 'react';
import { HuePicker } from 'react-color';
// AlphaPicker BlockPicker ChromePicker CirclePicker CompactPicker GithubPicker HuePicker MaterialPicker PhotoshopPicker SketchPicker SliderPicker SwatchesPicker TwitterPicker
import Parametro from './Parametro';
import Icon from './Icon';
import { Row, Col, Card, Form, Button, Tabs, Tab, Nav, InputGroup, FormControl, DropdownButton, Dropdown, Table } from 'react-bootstrap';
import Help from '../../../components/Help/Help';
import {lang, doc} from '../../../lang/lang';
import {aoa} from '../../../x';
import { useParams  } from "react-router-dom";
import ParametroList from './ParametroList';

const Item = (promp) => {
    // let {tp, id} = promp
    const { id } = useParams();
    const [huePickerColor, setHuePickerColor] = useState('#aaaaaa');
    const [parametros, setParametros] = useState([]);
    
    const [idc, setIdc] = useState(0);
    const delParam = (idc) => {setParametros(parametros.filter((item, i) => i != idc))}
    const upParam = () => {setParametros([...parametros, <Parametro id={idc} onLoad={setIdc(idc+1)} delParam={delParam} />])}
    
    const activo_tps = aoa.activo_tps

        useEffect(() => {
            setHuePickerColor(activo_tps[id].color[0]);
        });
        
        const bag = <React.Fragment>
            <Card>
                <Card.Header>
                    <Card.Title as="h5" >Modificar tipo de Activo</Card.Title>
                </Card.Header>
                <Card.Body>
                    <Form>
                        <Row>
                            <Col sm={12} md={2}>
                                <Form.Group controlId="formBasicEmail">
                                    <Form.Label>{lang.activo_tp}</Form.Label>
                                    <Form.Control type="text" placeholder={lang.activo_tp} value={activo_tps[id].de} />
                                </Form.Group>
                            </Col>
                            <Col sm={12} md={4}>
                                <Form.Group controlId="formBasicPassword">
                                    <Form.Label>{lang.descripcion}</Form.Label>
                                    <Form.Control type="text" placeholder={lang.descripcion} value={activo_tps[id].de} />
                                </Form.Group>
                            </Col>
                            <Col sm={12} md={3}>
                                <Form.Group controlId="exampleForm.ControlSelect1">
                                    <Form.Label>Icono del activo</Form.Label>
                                    <Icon id='icon_activo' img={activo_tps[id].icon} />
                                </Form.Group>
                            </Col>
                            <Col sm={12} md={3}>
                                <Form.Group controlId="formBasicPassword">
                                    <Form.Label>Color</Form.Label>
                                    <Form.Control type="text" value={`${huePickerColor}`}
                                        style={{
                                            backgroundColor: `${huePickerColor}`,
                                            border: "2px solid white"
                                        }}
                                    />
                                    <HuePicker color={huePickerColor} 
                                         onChange={(color) => {setHuePickerColor(color.hex);}}/>
                                </Form.Group>    
                            </Col>
                        </Row>
                        <hr />
                        <ParametroList id={id} />
                        { parametros}
                        <Row>
                            <Col md={6}>
                                <br />
                                <Button variant="primary" onClick={() => upParam()} >Agregar Parametro</Button>
                                <Button variant="primary" >Guardar</Button>
                            </Col>
                        </Row>
                    </Form>
                </Card.Body>
            </Card>
        </React.Fragment>
 
   /*

                   id={activo_tps[i].id}
                idx={activo_tps[i].identificador}
                icon={activo_tps[i].icon}
                de={activo_tps[i].de} 
                color1={activo_tps[i].color[0]}
                color2={activo_tps[i].color[1]}
*/

                      
    return (
        <React.Fragment>
            <Help contenido={'<b>' +doc.personas +':</b> ' +doc.personas +"<br /> " +doc.personas}/>
            {bag}       
        </React.Fragment> 
    );
};
export default Item;
